body{
    background-color: rgb(238, 238, 238);       
    width: 100% !important;    
    
    min-width: 1000px; 
}
            
*{
    font-family: Arial;
    margin: 0px;
    white-space: nowrap;
}

.postContainer {
    box-sizing: border-box;
    background: white;
    font-size: 2rem;
    padding: 3rem 0rem;
    background: linear-gradient(45deg, #e064da, #6f77e2);
    border-radius: 2rem;
    margin: 3rem;

}

.postForm {
    margin-top: 5%;
    text-align: center;
   
}

.inputField {
    text-align: center;
    width: 13%;
    height: 3vh;
    box-shadow: none !important;
    border-radius: 6px;
    margin-top: 10px;
    
}
.inputField:focus {
    outline: none;
    background-color: aliceblue;
}

.Author {
    margin-top: 110px;
    margin-right: 170px;
}

.post{
    display: inline-flex;
    flex-direction: row; 
    justify-content: center;
    margin-left: 40px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; 
    text-decoration: none;
    text-emphasis: none;
    word-wrap: break-word;
    min-width: 200px;
    width: 17%;
    background: linear-gradient(45deg, #e064da, #6f77e2);
    border-bottom: 4px solid rgb(65, 65, 65);
    height: 20vh;
    margin-top: 10px;
    padding-top: 10px;
    text-align: center;
    color: rgb(255, 255, 255);
    border-radius: 3px;
}

.post:hover {
    box-shadow: 5px 5px 5px gray;
}

.Header{
    width: 100%;
    /* min-width: fit-content; */

    word-wrap: break-word;
    /* max-width: max-content; */
    justify-content: flex-end;
    align-items: center;
    background: linear-gradient(45deg, #6f79e2, #cc64e6, #cc64e6, #6f79e2);
    height: 90px;
}

.homeTopStr{
    color: white;
    font-size: 44px;
    text-align: center;
}
/* HOME \/ */
.homeLinkPost{
    color: rgb(255, 255, 255);
    padding-top: 1px;
    text-emphasis: none;
    margin-left: 3%;
}
/* INSERT POSR */
.homeLinkHome{
    margin-left: 20px;
    color: rgb(255, 255, 255);
    padding-top: 1px;
    text-emphasis: none;
}
.homeLinkUsername {
    margin-left: 20px;
    color: rgb(255, 255, 255);
    padding-top: 1px;
    text-emphasis: none;
}

.homeLinkLogIn {
    margin-left: 70%;
    color: rgb(255, 255, 255);
    padding-top: 1px;
    text-emphasis: none;
}

.viewPostContainer {
    text-align: center;
}

.post-filter{
    background: linear-gradient(50deg, #ffffff, #e4e4e4, #e4e4e4, #ffffff);
    text-align: center;
    padding-top: 10px;
    padding-bottom: 20px;
    border-bottom: 2px solid rgb(255, 255, 255);
}
.post-filter input {
    border-radius: 10px;
    border: none;
    height: 4vh;
    width: 12rem;
}

.post-filter:focus input {
    border: none;
    outline: 0 none;
}

.logInContainer {
    margin-top: 40px;
    text-align: center;
}

.LateralBar {
    display: flex;
    justify-content: flex-end;
    margin-right: 30px;
    background-color: aliceblue;
}